import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getClosePengambilanBarcode } from "../../actions/closeBarcodeAction";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../../actions/LogicAction";
import {
  getListReturnPenjualan,
  getTotalNW,
} from "../../actions/returnPenjualanAction";
import { gagal, warning } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class FormBarcodeReturnCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "baki",
          text: "Nama Baki",
        },
        {
          dataField: "instansi",
          text: "Supplier",
        },
        {
          dataField: "jenis_trx",
          text: "Kode Barang",
        },
        {
          dataField: "total_bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "total_netto",
          text: "Netto(Gr)",
        },
        {
          dataField: "total_ongkos",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "total_gross",
          text: "Gross(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.pilihBarcode(row)}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getClosePengambilanBarcode());
  }

  async pilihBarcode(data) {
    let sebelum = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
    let hasil = data.detail_barang;
    hasil.forEach((list, index) => {
      AxiosMasterGet("jenis/get/by-kode-jenis/" + list.kode_jenis)
        .then((res) => {
          hasil[index].no_urut = index + 1;
          hasil[index].kadar_modal = res.data.kadar_beli;
          hasil[index].kadar_jual = res.data.kadar_jual;
          hasil[index].plastik = 0;
          hasil[index].kulit = 0;
          hasil[index].disc = 0;
          hasil[index].disc_persen = 0;
          hasil[index].netto_disc = hasil[index].netto;
          hasil[index].qty = 1;
          hasil[index].keterangan = "-";
          delete hasil[index].kadar;
          delete hasil[index].nama_jenis;
        })
        .then(() => {
          let posisi = sebelum.findIndex(
            (list) => list.netto === hasil[index].netto
          );
          if (posisi < 0) {
            sebelum.push(hasil[index]);
            localStorage.setItem("ReturnPenjualan", JSON.stringify(sebelum));
            var GrandTotalNetto = sebelum
              .map((list) => parseFloat(list.netto_disc))
              .reduce((a, b) => a + b, 0);
            // console.log(GrandTotalNetto);
            // return false;
            localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
            Swal.fire({
              position: "top-end",
              icon: "success",
              text: "Data Berhasil Ditambahkan!",
              showConfirmButton: false,
              timer: 1500,
            });
            this.props.dispatch(getListReturnPenjualan());
            this.props.dispatch(getTotalNW());
            this.props.dispatch(reset("FormTransaksiPenjualan"));

            this.props.dispatch(getNetto("ReturnPenjualan"));
            this.setState({
              discGlobalGr:
                (parseFloat(0) / 100) *
                parseFloat(localStorage.getItem("GrandTotalNetto")),
              discGlobalPr: 0,
              GrandTotalNetto: (
                parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
              ).toFixed(3),
            });
          } else {
            warning("Barcode sudah ada di list Barang");
          }
        })
        .catch((err) => gagal(`Error Get Data , Detail : ${err}`));
    });
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <GlobalTabel
          keyField="kodeJenis"
          data={
            this.props.listCloseBarcode.filter(
              (list) =>
                list.jenis_trx === "RETUR CUSTOMER" &&
                list.tanggal === getToday() &&
                list.instansi === this.props.kodeCustomer
            ) || []
          }
          columns={this.state.columns}
          textEmpty="Data Barcode Dengan Customer Tersebut Tidak Ada"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listCloseBarcode: state.provinsi.listCloseBarcode,
  };
})(FormBarcodeReturnCustomer);
